import { Global } from "../InfiniteHeights.Global";

const { ccclass, property } = cc._decorator;

@ccclass
export default class NameGame_Shop extends cc.Component {

    @property(cc.SpriteFrame)
    listSpfBallon: cc.SpriteFrame[] = [];

    @property(cc.Node)
    nBallon_0: cc.Node = null;

    @property(cc.Node)
    nBallon_1: cc.Node = null;

    @property(cc.Node)
    nBallon_2: cc.Node = null;

    @property(cc.Node)
    nNext: cc.Node = null;

    @property(cc.Node)
    nPrev: cc.Node = null;

    onLoad() {
        // index ballon hien thi
        // index ballon dc mo khoa
        Global.dataBallon = JSON.parse(cc.sys.localStorage.getItem('dataBallons')) || Global.dataBallon;
        Global.currentIndex = JSON.parse(cc.sys.localStorage.getItem('indexBallon')) || 0;

        console.log("dataBallon ", Global.dataBallon);
        console.log("CurrentIndex ", Global.currentIndex);
        this.updateShop()
    }

    start() {

    }

    onNext() {
        let nextIndex = Global.currentIndex + 1;
        console.log("OnNext ", nextIndex);
        if (Global.currentIndex > Global.unlockIndexBallon)
            Global.currentIndex = Global.unlockIndexBallon;

        this.updateShop()
    }

    onPrev() {
        Global.currentIndex--;
        if (Global.currentIndex < 0)
            Global.currentIndex = 0;

        this.updateShop()
    }

    updateShop() {
        // neu currentIndexBallon == 0 => an button Previous, Ballon 0
        // neu currentIndexBallon > 0 && < unlockIndexBallon => xu ly gi
        // neu currentIndexBallon == unlockIndexBallon =>an button Next, Ballon 2

        if (Global.currentIndex == 0) {
            this.nPrev.active = false;
            this.nBallon_0.active = false;
        }

        if(Global.currentIndex > 0  && Global.currentIndex < Global.unlockIndexBallon) {
            this.nPrev.active = true;
            this.nBallon_0.active = true;
            this.nBallon_1.active = true;
            this.nBallon_2.active = true;
        }
    }
    // update (dt) {}
}
